import axios, { type AxiosResponse } from 'axios'
import type { Dayjs } from 'dayjs'
import { type ChangeEvent, useEffect, useRef, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router-dom'

import MobileDatePicker from '@mui/lab/MobileDatePicker'
import {
  Box,
  Checkbox,
  Chip,
  Grid,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography
} from '@mui/material'

import { type ClubMember } from 'src/models/ClubMember'

import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

const CHIP_WIDTH = 87
const WHITE_SPACE_WIDTH = 76

interface ConferenceInfoProps {
  title: string
  changeTitle: (title: string) => void
  date: Dayjs | null
  changeDate: (date: Dayjs | null) => void
  selectedMembers: number[]
  selectAllMembers: (event: ChangeEvent<HTMLInputElement>, members: ClubMember[]) => void
  selectOneMember: (memberId: number) => void
  deleteOneMember: (memberId: number) => void
  selectAllLevelMember: (levelMembers: number[]) => void
  deleteAllLevelMember: (levelMembers: number[]) => void
}

const ConferenceInfo: React.FC<ConferenceInfoProps> = ({
  title,
  changeTitle,
  date,
  changeDate,
  selectedMembers,
  selectAllMembers,
  selectOneMember,
  deleteOneMember,
  selectAllLevelMember,
  deleteAllLevelMember
}) => {
  const [members, setMembers] = useState<ClubMember[]>([])
  const [levels, setLevels] = useState<string[]>([])
  const [selectedLevel, setSelectedLevel] = useState<string[]>([])
  const [isPausedListing, setIsPausedListing] = useState<boolean>(true)
  const params = useParams()
  const authHeader = useAuthHeader()

  const tableRef = useRef<HTMLDivElement>(null)
  const [tableWidth, setTableWidth] = useState(0)
  const NUM_OF_CHIP_STACK = Math.ceil((levels.length * CHIP_WIDTH) / (tableWidth - WHITE_SPACE_WIDTH))
  const NUM_OF_CHIP_PER_STACK = Math.ceil((tableWidth - WHITE_SPACE_WIDTH) / CHIP_WIDTH)

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/${params.id}/member`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<ClubMember[]>) => {
        const activeMembers = res.data.filter(member => member.deletedAt === null && member.firedAt === null)
        const sortedActiveMembers = activeMembers
        sortedActiveMembers.sort((a, b) => (a.name < b.name ? -1 : 1))
        setMembers(sortedActiveMembers)

        const levels = Array.from(
          new Set(
            activeMembers
              .filter((member: ClubMember) => member.level !== null)
              .map((member: ClubMember) => member.level)
          )
        ).sort()
        setLevels(levels)
      })
  }, [])

  useEffect(() => {
    setTableWidth(tableRef.current?.clientWidth || 450)
  }, [])

  useEffect(() => {
    window.addEventListener('resize', getTableWidth)
  }, [])
  const getTableWidth = () => {
    if (tableRef.current) {
      setTableWidth(tableRef.current.clientWidth)
    }
  }

  const isSelectedSomeMembers = selectedMembers.length > 0 && selectedMembers.length < members.length
  const isSelectedAllMembers = selectedMembers.length === members.length

  // 해당 level에 해당하는 모든 member가 선택되었는지 아닌지 체크 -> chip의 variant 변경 시 사용
  const isSelectedAllLevel = (level: string): boolean => {
    const levelMembers = members
      .filter((member: ClubMember) => member.level === level)
      .map((member: ClubMember) => member.id)
    const isAllLevelMemberSelected = levelMembers.every(memberId => selectedMembers.includes(memberId))
    return selectedLevel.includes(level) && isAllLevelMemberSelected
  }

  const handleSelectAllMembers: React.ChangeEventHandler<HTMLInputElement> = event => {
    selectAllMembers(event, members)
    setSelectedLevel(event.target.checked ? levels : [])
  }

  const handleSelectOneMember = (_event: ChangeEvent<HTMLInputElement>, memberId: number) => {
    const level = members
      .filter((member: ClubMember) => member.id === memberId)
      .map((member: ClubMember) => member.level)[0]
    if (!selectedMembers.includes(memberId)) {
      setSelectedLevel(prevSelected => Array.from(new Set([...prevSelected, level])))
      selectOneMember(memberId)
      return
    }
    if (
      members.filter((member: ClubMember) => member.level === level && selectedMembers.includes(member.id)).length === 1
    )
      setSelectedLevel(prevSelected => prevSelected.filter(lv => lv !== level))

    deleteOneMember(memberId)
  }

  // 각 level에 해당하는 chip을 선택 또는 해제했을 때 적용
  const toggleLevelChips = (level: string): void => {
    const levelMembers = members
      .filter((member: ClubMember) => member.level === level)
      .map((member: ClubMember) => member.id)
    if (!selectedLevel.includes(level)) {
      setSelectedLevel(prevSelected => [...prevSelected, level])
      selectAllLevelMember(levelMembers)
      return
    }
    setSelectedLevel(prevSelected => prevSelected.filter(lv => lv !== level))
    deleteAllLevelMember(levelMembers)
  }

  const handleIsPausedChange: React.ChangeEventHandler<HTMLInputElement> = event => {
    setIsPausedListing(() => event.target.checked)
  }

  return (
    <Grid container sx={{ justifyContent: 'center' }}>
      <Grid item xs={12} md={6}>
        <Box padding={{ xs: 3, md: 6 }} paddingBottom={{ xs: 0 }}>
          <Typography variant="h4" color={PureLightTheme.colors.primary.main} sx={{ margin: '20px 0px' }}>
            행사명
          </Typography>
          <TextField
            required
            id="title"
            label="행사명"
            value={title}
            onChange={event => changeTitle(event.target.value)}
            fullWidth
          />
          <Typography variant="h4" color={PureLightTheme.colors.primary.main} sx={{ margin: '20px 0px' }}>
            행사일
          </Typography>
          <MobileDatePicker
            label="행사일"
            inputFormat="yyyy/MM/dd"
            value={date}
            onChange={newValue => {
              changeDate(newValue)
            }}
            renderInput={params => <TextField {...params} sx={{ width: '100%' }} />}
          />
        </Box>
      </Grid>
      <Grid item xs={12} md={6}>
        <Box padding={{ xs: 3, md: 6 }}>
          <Box display="flex" justifyContent="space-between" alignItems="center" mt={1}>
            <Typography variant="h4" color={PureLightTheme.colors.primary.main} display="inline-block">
              참여인원
            </Typography>
            <Box>
              <Typography display="inline-block" variant="body1">
                휴회원 포함
              </Typography>
              <Checkbox sx={{ padding: '5px' }} checked={isPausedListing} onChange={handleIsPausedChange} />
            </Box>
          </Box>

          <TableContainer sx={{ maxHeight: '45vh', overflow: 'scroll' }} ref={tableRef}>
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isSelectedAllMembers}
                      indeterminate={isSelectedSomeMembers}
                      onChange={handleSelectAllMembers}
                    />
                  </TableCell>
                  <TableCell>
                    {levels
                      ? createChipHeaderComponent(
                          NUM_OF_CHIP_STACK,
                          NUM_OF_CHIP_PER_STACK,
                          CHIP_WIDTH,
                          levels,
                          isSelectedAllLevel,
                          toggleLevelChips
                        )
                      : '이름 | 등급'}
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {members &&
                  members.map(member => {
                    if (!isPausedListing && member.isPaused) {
                      return
                    }

                    const isMemberSelected = selectedMembers.includes(member.id)

                    return (
                      <TableRow hover key={member.id} selected={isMemberSelected}>
                        <TableCell padding="checkbox">
                          <Checkbox
                            color="primary"
                            checked={isMemberSelected}
                            onChange={(event: ChangeEvent<HTMLInputElement>): void =>
                              handleSelectOneMember(event, member.id)
                            }
                            value={isMemberSelected}
                          />
                        </TableCell>
                        <TableCell>
                          <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                            <Box style={{ display: 'flex', alignItems: 'baseline' }}>
                              {member.level ? `${member.name} | ${member.level}` : member.name}
                            </Box>
                          </Typography>
                          <Typography variant="body2" color="text.secondary" noWrap>
                            {member.studentId}
                          </Typography>
                        </TableCell>
                      </TableRow>
                    )
                  })}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
      </Grid>
    </Grid>
  )
}

export default ConferenceInfo

const createChipHeaderComponent = (
  numStack: number,
  numChip: number,
  chipWidth: number,
  levels: string[],
  isSelectedAllLevel: (level: string) => boolean,
  toggleLevelChips: (level: string) => void
): JSX.Element[] => {
  const chipIndexes = Array.from({ length: numStack }, (_, i) => i).reduce((acc, chipIndex) => {
    if (chipIndex * numChip >= levels.length) return acc
    return [...acc, chipIndex]
  }, [] as number[])

  return chipIndexes.map(chipIndex => (
    <Stack key={chipIndex} direction="row" sx={{ marginBottom: chipIndex === numStack - 1 ? 0 : 0.5 }}>
      {levels.slice(chipIndex * numChip, chipIndex * numChip + numChip).map(level => (
        <Chip
          key={level}
          label={level}
          sx={{ marginRight: 0.5, width: `${chipWidth}px` }}
          variant={isSelectedAllLevel(level) ? 'filled' : 'outlined'}
          onClick={() => toggleLevelChips(level)}
        />
      ))}
    </Stack>
  ))
}
