import { useEffect } from 'react'

import { Box, Card, Typography } from '@mui/material'
import { type Editor } from '@toast-ui/react-editor'

import { type Conference } from 'src/models/Conference'

import TextEditor from 'src/components/TextEditor'

const MAX_LOG_LENGTH = 31

interface ConferenceNoteProps {
  conference: Conference
  editorRef: React.RefObject<Editor>
}

const ConferenceNote: React.FC<ConferenceNoteProps> = ({ conference, editorRef }) => {
  useEffect(() => {
    editorRef.current?.getInstance().setHTML(conference.note || '')
  }, [])

  return (
    <Box padding={{ xs: 3, md: 6 }} paddingBottom={{ xs: 1, md: 3 }}>
      <Card>
        <TextEditor
          initialValue=""
          editorRef={editorRef}
          toolbarItems={[
            ['heading', 'bold', 'italic', 'strike'],
            ['hr', 'quote'],
            ['ul', 'ol', 'task'],
            ['table', 'link'],
            ['code', 'codeblock']
          ]}
        />
      </Card>
      <Box display="flex" justifyContent="right" sx={{ mr: 1, mt: 1 }}>
        <Typography variant="caption">{conference.log.length > MAX_LOG_LENGTH && conference.log}</Typography>
      </Box>
    </Box>
  )
}

export default ConferenceNote
