import axios, { type AxiosResponse } from 'axios'
import type { Dayjs } from 'dayjs'

import type { ClubId } from 'src/models/Club'
import type { Conference, ConferenceId } from 'src/models/Conference'
import { type DateProps } from 'src/models/date'

export interface DeleteParameters {
  clubId: number
  token: string
  conferenceId: ConferenceId
}

export const deleteConference = async ({ clubId, token, conferenceId }: DeleteParameters) => {
  await axios.delete<null, AxiosResponse<null>, null>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference?conferenceId=${conferenceId}`,
    {
      headers: { Authorization: token }
    }
  )
}

export interface GetParameters {
  clubId: number
  token: string
  date: DateProps
}

export type GetResponseData = Array<Conference>

export const getConferences = async ({ clubId, token, date }: GetParameters) => {
  const response = await axios.get<GetResponseData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference?year=${date.year}&semester=${date.semester}`,
    {
      headers: { Authorization: token }
    }
  )
  return response.data
}

interface PutConferenceProps {
  token: string
  clubId: ClubId
  conferenceId: number
  title: string
  startAt: Dayjs | null
  users: number[]
  note: string
  clubDate: DateProps
}

export const putConferences = async ({
  token,
  clubId,
  conferenceId,
  title,
  startAt,
  users,
  note,
  clubDate
}: PutConferenceProps) => {
  const { data } = await axios.put(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference/${conferenceId}`,
    {
      title: title,
      startAt: startAt,
      users: users,
      note: note
    },
    {
      params: clubDate,
      headers: { Authorization: token }
    }
  )

  return data
}
