import dayjs, { type Dayjs } from 'dayjs'
import { useSnackbar } from 'notistack'
import { type ChangeEvent, useRef, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useNavigate, useParams } from 'react-router-dom'

import {
  Card,
  Checkbox,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'
import type { Editor } from '@toast-ui/react-editor'

import { putConferences } from 'src/api/conference'

import { type ClubMember } from 'src/models/ClubMember'
import type { Conference } from 'src/models/Conference'
import type { DateProps } from 'src/models/date'
import { defaultDate } from 'src/models/date'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

import ConferenceInfo from '../Conference/ConferenceInfo'
import ConferenceNote from '../Conference/ConferenceNote'

interface AttendanceTableProps {
  className?: string
  conference: Conference
  handleAttendanceCheck(conferenceId: number, userId: number, isAttendance: boolean): Promise<void>
}

const AttendanceTable: React.FC<AttendanceTableProps> = ({ conference, handleAttendanceCheck }) => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const navigate = useNavigate()

  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const { userInfo } = useSafeAuthUser()
  const editorRef = useRef<Editor>(null)

  const { id } = useParams()
  const clubId = Number(id)

  const club = userInfo?.clubs.find(club => club.id === clubId)
  const clubDate: DateProps = { year: club?.year || defaultDate.year, semester: club?.semester || defaultDate.semester }

  const [title, setTitle] = useState<string>(conference.title)
  const [startDate, setStartDate] = useState<Dayjs | null>(dayjs(conference.startAt))
  const [selectedMembers, setSelectedMembers] = useState<number[]>(conference.member.map(member => member.id))

  const changeTitle = (title: string) => {
    setTitle(title)
  }

  const changeStartDate = (startDate: Dayjs | null) => {
    setStartDate(startDate)
  }

  const selectAllMembers = (event: ChangeEvent<HTMLInputElement>, members: ClubMember[]) => {
    setSelectedMembers(event.target.checked ? members && members.map(member => member.id) : [])
  }

  const selectOneMember = (memberId: number) => {
    setSelectedMembers(prevSelected => [...prevSelected, memberId])
  }

  const deleteOneMember = (memberId: number) => {
    setSelectedMembers(prevSelected => prevSelected.filter(id => id !== memberId))
  }

  const selectAllLevelMember = (levelMembers: number[]) => {
    setSelectedMembers(prevSelected => Array.from(new Set([...prevSelected, ...levelMembers])))
  }

  const deleteAllLevelMember = (levelMembers: number[]) => {
    setSelectedMembers(prevSelected => prevSelected.filter(memberId => !levelMembers.includes(memberId)))
  }

  const handleSubmit = async () => {
    // 행사명, 행사일자, 참여인원 수정 api 들어갈 곳
    if (title.length > 50) {
      enqueueSnackbar('행사명은 50자 이내로 작성해 주십시오.', { variant: 'error' })
      return
    }
    try {
      const data = await putConferences({
        token: authHeader(),
        conferenceId: conference.id,
        startAt: startDate,
        users: selectedMembers,
        note: editorRef.current?.getInstance().getHTML() ?? '',
        clubId,
        title,
        clubDate
      })

      if (data.message === 'ok') {
        enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
        navigate(`/manage/${clubId}/attendance`)
      } else {
        enqueueSnackbar('오류가 발생했습니다. 다시 시도해 주십시오.', { variant: 'error' })
      }
    } catch (e) {
      enqueueSnackbar('오류가 발생했습니다. 다시 시도해 주십시오.', { variant: 'error' })
    }
  }

  return (
    <Card>
      <ConferenceInfo
        title={title}
        changeTitle={changeTitle}
        date={startDate}
        changeDate={changeStartDate}
        selectedMembers={selectedMembers}
        selectAllMembers={selectAllMembers}
        selectOneMember={selectOneMember}
        deleteOneMember={deleteOneMember}
        selectAllLevelMember={selectAllLevelMember}
        deleteAllLevelMember={deleteAllLevelMember}
      />
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>이름</TableCell>
              {matches && <TableCell>학번</TableCell>}
              {matches && <TableCell>학과</TableCell>}
              {matches && <TableCell>비고</TableCell>}
              <TableCell>출석 여부</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {conference.member.map(member => {
              return (
                <TableRow hover key={member.id}>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                      {member.name}
                      {!matches && (
                        <Typography variant="subtitle1" gutterBottom noWrap>
                          {member.studentId}
                        </Typography>
                      )}
                    </Typography>
                  </TableCell>

                  {matches && (
                    <TableCell>
                      <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                        {member.studentId}
                      </Typography>
                    </TableCell>
                  )}
                  {matches && (
                    <TableCell>
                      <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                        {member.department}
                      </Typography>
                    </TableCell>
                  )}
                  {matches && (
                    <TableCell>
                      <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                        {member.note}
                      </Typography>
                    </TableCell>
                  )}

                  <TableCell>
                    <Checkbox
                      defaultChecked={member.attendanceType}
                      onChange={e =>
                        handleAttendanceCheck(conference.id, member.id, (e.target as HTMLInputElement).checked)
                      }
                    />
                  </TableCell>
                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <ConferenceNote conference={conference} editorRef={editorRef} />
      <CancelSubmitBtns submitBtnText="등록/수정" handleSubmit={handleSubmit} />
    </Card>
  )
}

export default AttendanceTable
